@font-face {
  font-family: 'fa-viber';
  src:url('../fonts/fa-viber.eot?-3dwksg');
  src:url('../fonts/fa-viber.eot?#iefix-3dwksg') format('embedded-opentype'),
  url('../fonts/fa-viber.ttf?-3dwksg') format('truetype'),
  url('../fonts/fa-viber.woff?-3dwksg') format('woff'),
  url('../fonts/fa-viber.svg?-3dwksg#fa-viber') format('svg');
  font-weight: normal;
  font-style: normal;
}

.fa-viber:before {
  font-family: 'fa-viber';
  content: "\e666";
}


@font-face {
  font-family: 'theme';
  src:url('../fonts/theme/theme.eot');
  src:url('../fonts/theme/theme.eot?#iefix') format('embedded-opentype'),
  url('../fonts/theme/theme.ttf') format('truetype'),
  url('../fonts/theme/theme.woff') format('woff'),
  url('../fonts/theme/theme.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

